@mixin stitches-sprite($x: 0, $y: 0, $width: 0, $height: 0){
    background-position: $x $y;
    width: $width;
    height: $height;
    margin: 5px auto;
    background-image: url('./assets/images/skills-sprite-1.png');
}

@mixin stitches-sprite-1($x: 0, $y: 0, $width: 0, $height: 0){
    background-position: $x $y;
